<template>
  <div class="goods" id="goods" name="goods">

      <div v-if="IsShop">
              <div class="about" id="about" name="about">
        <div class="about-header">
          <div class="about-title">
            <i class="el-icon-goods" style="color: #ff6700;"></i>
            {{shopName}}
     
          </div>
    
        </div>
 
      </div>
                <!-- 分类标签 -->
                <div class="nav">
                  <div class="product-nav">
                    <el-tabs   @tab-click="handleClick" value="first">
                      <el-tab-pane label="店铺商品" name="first"> </el-tab-pane>
                      <el-tab-pane label="店铺订单" name="second"> </el-tab-pane>
                      <el-tab-pane label="店铺详情" name="third"> </el-tab-pane>
                    </el-tabs>
                  </div>
                </div>
                <!-- 分类标签END -->

                <!-- 主要内容区 -->
                <div class="main" v-show="isshow">
                  <div class="list">
                    <MyList :list="product" v-if="product.length>0" ></MyList>
                    <div v-else class="none-product">抱歉没有找到相关的商品，请看看其他的商品</div>
                  </div>
                  <!-- 分页 -->
                  <div class="pagination">
                    <el-pagination
                      background
                      layout="prev, pager, next"
                      :page-size="pageSize"
                      :total="total"
                      @current-change="currentChange"
                    ></el-pagination>
                  </div>
                  <!-- 分页END -->
                </div>

                <div class="main" v-show="ordershow">
                <div class="order">
                <!-- 我的订单主要内容 -->
                <div class="order-content" v-if="orders!==null">
                  <div class="content" v-for="(item,index) in orders" :key="index">
                      <ul>
                        <!-- 我的订单表头 -->
                        <li class="order-info">
                          <div class="order-id">订单编号: {{orders[index].order_id}}</div>
                          <div class="order-time">订单时间: {{orders[index].createTime | dateFormat}}</div>
                          
                        </li>
                        <li class="header">
                          <div class="pro-img"></div>
                          <div class="pro-name">商品名称</div>
                          <div class="pro-price">单价</div>
                          <div class="pro-num">数量</div>
                          <div class="pro-total">小计</div>
                          <div class="pro-operate">操作</div>
              
                        </li>
                        <!-- 我的订单表头END -->

                        <!-- 订单列表 -->
                        <li class="product-list"  >
                          <div class="pro-img">
                            <router-link :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }">
                              <img :src="orders.goodsCoverImg" />
                            </router-link>
                          </div>
                          <div class="pro-name">
                            <router-link
                              :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }"
                            >{{orders[index].goodsName}}</router-link>
                          </div>
                          <div class="pro-price">{{orders[index].sellingPrice}}元</div>
                          <div class="pro-num">{{orders[index].goodsCount}}</div>
                          <div class="pro-total pro-total-in">{{orders[index].sellingPrice*orders[index].goodsCount}}元</div>
                        
                          <div class="pro-operate">
                            <el-button type="primary" size="mini" v-show="orders[index].orderStatus == 0">拒绝</el-button>
                            <el-button type="success" size="mini" v-show="orders[index].orderStatus == 1">退款</el-button>
                            <el-button type="warning" size="mini" v-show="orders[index].orderStatus == 1">发货</el-button>
                            <!-- <el-button type="danger" size="mini" v-show="orders[index].orderStatus == 3">退货</el-button> -->
                            <p v-show="orders[index].orderStatus == 2">等待签收</p>
                            <p v-show="orders[index].orderStatus == 3">订单完成</p>
                          </div>
                        </li>
                      </ul>
                        <div class="order-bar">
                          <div class="order-bar-right">
                            <span>
                              <span class="total-price-title">状态：</span>
                              <span class="total-price">{{orderst[orders[index].orderStatus]}}  </span>
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                      <!-- 订单列表END -->
                </div>
              </div>
                <!-- 主要内容区END -->
      </div>
    <div v-else class="order-empty">
      <div class="empty">
        <h2>您还没有店铺</h2>
        <p>快去开通店铺叭！</p>
    
       <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
          <el-form-item label="名称">
            <el-input v-model="formLabelAlign.name"></el-input>
          </el-form-item>
          <el-form-item label="店铺介绍">
            <el-input v-model="formLabelAlign.region"></el-input>
          </el-form-item>
            <el-form-item>
            <el-button type="primary" @click="onCreatshop">立即创建</el-button>
          </el-form-item>
        </el-form>  
      </div>
    </div>
  
  </div>
</template>
<script>
// import MyMarkdown from "../components/MyMarkdown";
import 'element-ui/lib/theme-chalk/display.css';
export default {
  components: {
   },
   data(){
     return{
       labelPosition: 'right',
        formLabelAlign: {
          name: '',
          region: '',
          type: ''
        },

      shopName:"",
      shopId:"",
      shopIntroduce:"",

       IsShop:false,
       pageSize:15,
       total:0,
       currentPage:0,
       isshow:true,
       ordershow:false,
             orderst:["待支付","已支付","已发货","已签收"],

       orders:"",
         product:"",
     }
   },
   created(){
    this.isShop() ;
 
   },
  watch: {
    // 通过订单信息，计算出每个订单的商品数量及总价
    IsShop: function(val) {
      if(val)
      this.getData()
    }
  },
    methods: {
      onCreatshop(){
           this.shopName=this.formLabelAlign.name,
            this.shopIntroduce=this.formLabelAlign.region;
            let as = this.$store.getters.getUserId;
              this.$axios(   
            {
              // url: this.$target+"/store/register", 
              url: "http://localhost:3000/cart", 
              method: 'post',
              params:{
                userId:as,
                storeName:this.formLabelAlign.name,
                introduceSign:this.formLabelAlign.region,
              }
        })
        .then(res => {
          this.IsShop = res.data.flag
           if(res.data.flag)
          {
            this.shopId = res.data.data.storeId
            this.notifySucceed(res.data.msg)
          }
          else{
            this.notifyError(res.data.msg)
          }
         })
        .catch(err => {
          return Promise.reject(err);
        }); 
       
      },
          // 页码变化调用currentChange方法
      currentChange(currentPage) {
      this.currentPage = currentPage;
       
      },
      // 页码变化调用currentChange方法
      isShop() {
              let as = this.$store.getters.getUserId;

             this.$axios(   
            {
              url: this.$target+"/store/isStore", 
              // url: "http://localhost:3000/cart", 
              method: 'get',
              params:{
                userId:as
              }
        })
        .then(res => {
          console.log("res.data.data",res.data.data);

          this.IsShop =res.data.flag;
          if(res.data.flag)
          { 
            this.shopName=res.data.data.storeName,
            this.shopId=res.data.data.storeId,
            this.shopIntroduce=res.data.data.introduceSign,
            this.notifySucceed(res.data.msg)
          }
          else{
            this.notifyError(res.data.msg)
          }
         //  this.total = res.data.total;
        })
        .catch(err => {
          return Promise.reject(err);
        }); 
       
      },
 
      handleClick(tab, event) {
        console.log(tab, event);
        console.log(tab.index);
        if(tab.index==0){
        this.isshow = true
        this.ordershow = false
        }
        else if(tab.index==1){
        this.isshow = false
        this.ordershow = true;
        console.log("返回给");
        this.indexorder();
        }
        else{
          console.log("error");
        }
         console.log("this.isshow",this.isshow);
         console.log(" isshow",this.orders);
      },
      getData() {
      // 如果分类列表为空则请求全部商品数据，否则请求分类商品数据
        const api = "/store/allGoods"; //请求分类数据
       this.$axios(   
            {
              url: this.$target+api, 
              // url: "http://localhost:3000/cart", 
              method: 'get',
              params:{
               StoreId:this.shopId,
 
              }
        })
        .then(res => {
           this.product = res.data.data;
        //  this.total = res.data.total;
        })
        .catch(err => {
          return Promise.reject(err);
        }); 
    },
    indexorder(){
        this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/store/allOrders",
              method: 'get',
              params:{
                  StoreId:this.shopId,  
              }
        })
        .then(res => {
           if(res.data.flag){
              this.orders = res.data.data;
             }
            else{
              this.notifyError("获取所有订单",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
    }
    }

};
</script>
<style scoped>
.about {
  background-color: #f5f5f5;
}
.about .about-header {
  height: 64px;
  background-color: #fff;
  border-bottom: 2px solid #ff6700;
}
.about .about-header .about-title {
  width: 1225px;
  margin: 0 auto;
  height: 64px;
  line-height: 64px;
  font-size: 28px;
}
.goods {
  background-color: #f5f5f5;
}
/* 面包屑CSS */
.el-tabs--card .el-tabs__header {
  border-bottom: none;
}
.goods .breadcrumb {
  height: 50px;
  background-color: white;
}
.goods .breadcrumb .el-breadcrumb {
  width: 1225px;
  line-height: 30px;
  font-size: 16px;
  margin: 0 auto;
}
/* 面包屑CSS END */

/* 分类标签CSS */
.goods .nav {
  background-color: white;
}
.goods .nav .product-nav {
  width: 1225px;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
}
.nav .product-nav .title {
  width: 50px;
  font-size: 16px;
  font-weight: 700;
  float: left;
}
/* 分类标签CSS END */

/* 主要内容区CSS */
.goods .main {
  margin: 0 auto;
  max-width: 1225px;
}
.goods .main .list {
  min-height: 650px;
  padding-top: 14.5px;
  margin-left: -13.7px;
  overflow: auto;
}
.goods .main .pagination {
  height: 50px;
  text-align: center;
}
.goods .main .none-product {
  color: #333;
  margin-left: 13.7px;
}
/* 主要内容区CSS END */

/* 我的订单头部CSS END */
.order .content {
  width: 1225px;
  margin: 0 auto;
  background-color: #fff;
  margin-bottom: 50px;
}

.order .content ul {
  background-color: #fff;
  color: #424242;
  line-height: 85px;
}
/* 我的订单表头CSS */
.order .content ul .order-info {
  height: 60px;
  line-height: 60px;
  padding: 0 26px;
  color: #424242;
  border-bottom: 1px solid #ff6700;
}
.order .content ul .order-info .order-id {
  float: left;
  color: #ff6700;
}
.order .content ul .order-info .order-time {
  float: right;
}

